@tailwind base;
@tailwind components;
@tailwind utilities;
html {
  overflow-x: hidden;
  scroll-behavior: smooth;
  scrollbar-width: none;
  scrollbar-color: rgb(12, 0, 19) rgb(206, 209, 0);
}
* {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center .slick-center #div_see {
  margin: 0% 0%;
  height: 400px;
  transform: scale(1.65, 1);
  z-index: 20;
  transition: 0.3s ease;
  pointer-events: all;
}
.center #div_see {
  margin: 10% 0%;
  pointer-events: none;
}
.center .slick-center #div_see img {
  width: 100%;
  height: 100%;
}
.slick-slider {
  width: 100%;
  margin-top: 5%;
  padding: 0% 2em;
}
.slick-dots {
  position: relative;
  right: 0vw;
  bottom: 0vh;
}
.slick-next {
  position: absolute;
  margin-right: 3%;
}
.slick-prev {
  position: absolute;
  margin-left: 2%;
  z-index: 10;
}
.slick-prev {
  font-size: 100px;
  scale: 4;
  background-color: yellow;
}

/* blogs */
#div_see:hover #div_see_text {
  transition: 0.5s ease-in-out;
  top: 0%;
  padding: 50% 0%;
  background: linear-gradient(0deg, rgb(0, 0, 0, 0.8), rgba(56, 56, 56, 0.389));
  width: 100%;
  height: 100%;
}
#dangerouslySetInnerHTML p {
  text-transform: none;
  font-family: Finlandica, serif;
  font-feature-settings: normal;
  font-style: normal;
  letter-spacing: 0.005684210673758858em;
  line-break: auto;
  line-height: 1.4736842105263157em;
  font-size: 19px;
  font-weight: 400;
  overflow-wrap: normal;
  color: rgba(26, 26, 26, 1);
}
#dangerouslySetInnerHTML li {
  font-size: large;
  line-height: 1.25;
  margin: 2% 0%;
  color: #261d1d;
}
.dark #dangerouslySetInnerHTML li {
  color: rgb(223, 223, 223);
}
#dangerouslySetInnerHTML a {
  color: rgb(0, 179, 255);
}
#dangerouslySetInnerHTML iframe {
  width: 100%;
  min-height: 70vh;
}
.dark #dangerouslySetInnerHTML a {
  color: rgba(90, 184, 255, 0.809);
}
.dark #dangerouslySetInnerHTML p {
  color: rgb(145, 166, 175);
}
.ql-syntax {
  padding: 2% 0%;
  background-color: black;
  color: yellow;
}
@media screen and (max-width: 768px) {
  #hamburger {
    padding: 1% 1%;
    position: fixed;
    transition: 0.2s ease-in;
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
  }
  #menu_item {
    margin: 1% 2%;
    padding: 3% 0%;
  }
  .slick-slider {
    padding: 1%;
  }
}
